.App {
  align-content: center;
}

.container {
  display: grid;
}
.side {
  top: 0;
  position: fixed;
  grid-row: 1/6;
  grid-column: 1/2;
  width: 15vw;
  height: 100vh;
  min-width: 120px;
  background-color: black;
  color: aliceblue;
  list-style: none;
}

/********* SIDEBAR ********/

.side {
  list-style: none;
  text-decoration: none;
}

.logo {
  margin-left: 40px;
  margin-top: 20px;
}
.side #home-icon {
  margin-top: 30px;
  margin-bottom: 8px;
  padding: 8px;
  margin-left: 12px;
  margin-right: 12px;
  color: #b3b3b3;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}

#home-icon:focus {
  background-color: rgb(39, 39, 39);
  color: white;
}

#home-icon:hover {
  background-color: none;
  color: white;
}
